.page_404 {
    padding: 40px 0;
    background: #fff;
    font-family: "Arvo", serif;
    margin-top: 50px;
}

.page_404 img {
    width: 100%;
}

.four_zero_four_bg {
    background-image: url(https://cdn.dribbble.com/users/285475/screenshots/2083086/dribbble_1.gif);
    height: 400px;
    background-position: center;
}

.four_zero_four_bg h1 {
    font-size: 80px;
}

.four_zero_four_bg h3 {
    font-size: 80px;
}

.link_404 {
    color: #fff !important;
    padding: 10px 20px;
    background: -webkit-linear-gradient(45deg, #f3c5bd 0%, #e86c57 0%, #cd4631 0%, #ff6600 75%, #c72200 100%);
    margin: 20px 0;
    display: inline-block;
}

.link_404:hover {
    background: -webkit-linear-gradient(45deg, #ea5507 -1%, #feccb1 0%, #f17432 0%, #ea5507 0%, #fb955e 100%);
}

.contant_box_404 {
    margin-top: -50px;
}

.h2 {
    font-size: 30px;
    text-transform: capitalize;
    margin-top: 30px;
}

.contant_box_404 p {
    font-size: 18px;
}